.nav {
  font-family: "PTSans-Bold";
  font-size: 18px;
  line-height: 40px;
  display: block;
  width: 100%;
}

.nav__list{
  margin: 0;
  padding: 0;
  list-style: none;
}

.nav__link{
  text-decoration: none;
}


@media (min-width: @mobile-width) {

.nav__list {
    margin: 0;
    padding: 0;
    min-width: 320px;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.nav__item{
  margin: 0;
  padding: 0;
  height: 55px;
  overflow: hidden;
  display: flex;
  align-items: center;
  background-color: @blue;
  border-bottom: 1px solid rgba(1, 1, 1, 0.2);
  }

.nav__item--logo {
  background-color: @white;
  order: 1;
    }


.nav__item svg{
  fill: @white;
}

.nav__link{
  color: @white;
  margin: 0 auto;
}

}

@media (min-width: @tablet-width) {

  .nav__list{
    min-width: 768px;
    width: 100%;
    min-height: 56px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
    }

  .nav__item{
    background-color: @white;
    width: 153px;
    height: 55px;
    border-bottom: none;
    }

    .nav__item--logo {
     order: unset;
    }

    .nav__link{
    color: @black;
    padding: 0;
    margin: 0 auto;
    }

    .nav__item svg{
    display: none;
    }

}

@media (min-width: @desktop-width) {

  .nav__list{
    min-width: 1200px;
    width: 100%;
    color: @black;
    background-color: @white;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
    min-height: 56px;
  }

  .nav__item--logo {
     order: unset;
  }


  .nav__link{
    color: @deepblack;
    padding: 0;
    margin: 0 auto;
    }

  .nav__item svg{
    display: none;
  }

}
