    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    height: 80px;
  }
  .photopage__namewrapper:first-child {
    width: 648px;
  }
  .photopage__itemname,
  .photopage__like {
    display: flex;
  }
  .photopage__itemname {
    flex-direction: column;
    justify-content: center;
  }
  .photopage__like {
    flex-direction: row;
    justufy-content: center;
  }
